body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  margin: 0 auto;
  padding: 20px;
}

.search-form {
  justify-content: center;
  margin-bottom: 20px;
  display: flex;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.photo-card {
  width: 350px;
  background-color: #f9f9f9;
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(0, 0, 0, .3);
}

.photo-card img {
  width: 100%;
  border-radius: 5px;
}

.info {
  gap: 10px;
  padding: 5px;
  display: flex;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px 20px;
  display: none;
}

.load-more:hover {
  background-color: #0056b3;
}

